@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@500;700&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  width: 100%;
  display: block;
}

ul {
  list-style-type: none;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  text-align: left;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --white: 0 0% 98%;
  --medium-gray: 0 0% 41%;
  --black: 0 0% 8%;
  --selection-bg: 277 100% 50%;
}

/**
 * ### Font family 
 */
/**
 * ### Size variables
 */
/**
 * ### Font weight
 */
::selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

::-moz-selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

html {
  font-size: 100%;
  font-family: "Epilogue", sans-serif;
}

body {
  background-color: hsl(var(--white));
  font-size: 0.9375rem;
}

p {
  font-size: 1.125rem;
}

p:not([class]) {
  max-width: 45ch;
}

/**
 * Colors
 */
.bg-white {
  background-color: hsl(var(--white));
}

.clr-white {
  color: hsl(var(--white));
}

.bg-black {
  background-color: hsl(var(--black));
}

/**
 * Position
 */
.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

/**
 * ### With container
 */
.container {
  --max-width: 1440px;
  --container-padding: 1rem;
  width: min(var(--max-width), 100% - var(--container-padding) * 0);
  margin-inline: auto;
}

/**
 * ### Flex 
 */
.flex {
  display: flex;
}

.flex-dir-col {
  flex-direction: column;
}

.justify-cont-betw {
  justify-content: space-between;
}

/**
 * ### Grid 
 */
.grid-columns {
  display: grid;
  align-content: space-around;
}
@media (min-width: 52.5em) {
  .grid-columns {
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
  }
}

/**
 * ### Gap 
 */
.gap-1 {
  gap: 1rem;
}

.gap-2 {
  gap: 2rem;
}

.gap-3 {
  gap: 3rem;
}

/**
 * Float
 */
.float-l {
  float: left;
}

.float-r {
  float: right;
}

/**
 * ### Align items center 
 */
.align-itm-center {
  align-items: center;
}

/**
 * ### Justify content center , start
 */
.justify-cont-center {
  justify-content: center;
}

.justify-cont-start {
  justify-content: start;
}

/**
 * ### Justify items center , start
 */
.justify-item-center {
  justify-items: center;
}

.justify-items-start {
  justify-items: start;
}

/**
 * ### Text  center 
 */
.text-align-center {
  text-align: center;
}

/**
 * Flow -Creating an upper edge for all descendants except the first.
 * inline style --> style="--flow-spacer: 1.5rem"
 */
:where(.flow :not(:first-child)) {
  margin-top: var(--flow-spacer, 1em);
}

/**
 * ### Margin Padding position
 */
.margin-bottom-700 {
  margin-block-end: 3rem;
}

/**
 * Padding top and bottom
 */
.padding-block-600 {
  padding-block: 2rem;
}

.padding-block-700 {
  padding-block: 3rem;
}

.padding-block-900 {
  padding-block: 5rem;
}

.padding-lr-400 {
  padding-inline: 1rem;
}

.padding-lr-600 {
  padding-inline: 2rem;
}

.padding-lr-800 {
  padding-inline: 4rem;
}

/**
 * ### Font Size
 */
.fs-400 {
  font-size: 1rem;
}

.fs-600 {
  font-size: 2rem;
}

.fs-700 {
  font-size: 3rem;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap; /* added line */
  border: 0;
}

.btn {
  display: inline-flex;
  border: 0;
  padding: 1em 2em;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.btn:focus {
  outline: none;
  outline-offset: -4px;
}

.btn:active {
  transform: scale(0.99);
}

.primary-header {
  display: flex;
}
.primary-header picture {
  max-width: 6.25rem;
  margin-top: 0.9375rem;
  margin-right: 3.125rem;
}

.logo {
  width: 100%;
}

/**
 * BUTTON
 */
.mobile-nav-toggle {
  display: block;
  position: absolute;
  z-index: 2000;
  right: 1rem;
  top: 2rem;
  background: transparent;
  background-image: url(/images/icon-menu.svg);
  background-repeat: no-repeat;
  background-position: center;
  width: 1.5rem;
  aspect-ratio: 1;
  border: 0;
}
@media (min-width: 52.5em) {
  .mobile-nav-toggle {
    display: none;
  }
}

.mobile-nav-toggle[aria-expanded=true] {
  background-image: url(/images/icon-close-menu.svg);
}

.mobile-nav-toggle:focus-visible {
  outline: 0.3125rem solid white;
  outline-offset: 0.3125rem;
}

/**
 * NAV
 */
.primary-navigation {
  position: fixed;
  inset: 0;
  width: 100%;
  display: grid;
  z-index: 100;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  flex-direction: column;
  transform: translateX(100%);
  transition: transform 500ms ease-in-out;
}
@media (min-width: 52.5em) {
  .primary-navigation {
    display: flex;
    position: relative;
    inset: 0;
    transform: translateX(0%);
  }
}

.primary-navigation[data-visible=true] {
  transform: translateX(0);
}

.col-1 {
  grid-column: span 1/2;
  background-color: hsl(var(--black)/0.9);
}
@media (min-width: 52.5em) {
  .col-1 {
    display: none;
  }
}

.col-2 {
  grid-column: span 2/4;
  background-color: hsl(var(--white));
  padding: 5rem 1.875rem 0rem 1.875rem;
}
@media (min-width: 52.5em) {
  .col-2 {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    grid-column: span 1/4;
    padding: 0;
  }
}

.nav-menu {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  margin-bottom: 1.25rem;
}
@media (min-width: 52.5em) {
  .nav-menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.nav-menu li a {
  font-size: 1.125rem;
  color: hsl(var(--medium-gray));
  text-decoration: none;
}

.nav-buttons {
  display: flex;
  flex-direction: column;
}
@media (min-width: 52.5em) {
  .nav-buttons {
    display: flex;
    flex-direction: row;
    align-items: baseline;
  }
}

/**
 * DROPDOWN MENU
 */
button.accordion {
  border: 0;
  background-color: hsl(var(--white));
  color: hsl(var(--medium-gray));
  font-size: 1.125rem;
  margin-bottom: 0.625rem;
}
button.accordion::after {
  content: url("/images/icon-arrow-down.svg");
  margin-left: 0.625rem;
}

.features ul,
.company ul {
  display: none;
}

.dropmenu-1 li,
.dropmenu-2 li {
  font-size: 1.125rem;
  color: hsl(var(--medium-gray));
  padding: 0.5rem;
}

.dropmenu-1 li:nth-child(1)::before {
  content: url("/images/icon-todo.svg");
  padding-right: 0.625rem;
}

.dropmenu-1 li:nth-child(2)::before {
  content: url("/images/icon-calendar.svg");
  padding-right: 0.625rem;
}

.dropmenu-1 li:nth-child(3)::before {
  content: url("/images/icon-reminders.svg");
  padding-right: 0.625rem;
}

.dropmenu-1 li:nth-child(4)::before {
  content: url("/images/icon-planning.svg");
  padding-right: 0.625rem;
}

/**
 * BUTTONS
 */
button.login,
button.register {
  display: flex;
  justify-content: center;
  color: hsl(var(--medium-gray));
  outline: 0.125rem solid hsl(var(--medium-gray));
  background-color: hsl(var(--white));
  border-radius: 0.75rem;
  transition: 250ms ease-in-out;
}
button.login:hover, button.login:focus,
button.register:hover,
button.register:focus {
  color: hsl(var(--black));
}

button.login {
  outline: 0;
}

.grid-hero {
  display: grid;
}
@media (min-width: 52.5em) {
  .grid-hero {
    align-content: space-around;
    place-items: center;
    display: grid;
    grid-template-columns: minmax(1rem, 1fr) repeat(2, minmax(0, 30rem)) minmax(1rem, 1fr);
  }
}

.grid-hero > *:first-child {
  grid-column: 1;
  grid-row: 2;
}
@media (min-width: 52.5em) {
  .grid-hero > *:first-child {
    grid-row: 1;
    grid-column: 2;
  }
}

.grid-hero > *:last-child {
  grid-column: 1;
  grid-row: 1;
}
@media (min-width: 52.5em) {
  .grid-hero > *:last-child {
    grid-row: 1;
    grid-column: 3;
  }
}

.hero-image picture {
  width: 100%;
}

section.hero-info {
  text-align: center;
}
@media (min-width: 52.5em) {
  section.hero-info {
    text-align: left;
  }
}

h1 {
  font-size: clamp(1.75rem, 1.375rem + 4vw, 6.25rem);
  font-weight: 700;
}

p {
  margin: 0 auto;
  color: hsl(var(--medium-gray));
}
@media (min-width: 52.5em) {
  p {
    margin: 0;
  }
}

section button.hero-btn {
  color: hsl(var(--white));
  background-color: hsl(var(--black));
  border-radius: 0.75rem;
  transition: 250ms ease-in-out;
}
section button.hero-btn:hover, section button.hero-btn:focus {
  color: hsl(var(--black));
  outline: 0.0625rem solid hsl(var(--black));
  background-color: hsl(var(--white));
}

.logos-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.875rem;
  width: 100%;
}

.logos-container picture {
  max-width: 6.25rem;
}
@media (min-width: 52.5em) {
  .logos-container picture {
    max-width: 12.5rem;
  }
}

.logos-container picture img {
  width: 100%;
  height: auto;
}